
.block {
  width: 50px;
  height: 50px;
  border-bottom: 1px solid #3A3A3A;
  border-right: 1px solid #3a3a3a;

  box-sizing: border-box;
  position: absolute;

}

.s1 {
  left: 0px;
}

.s2 {
  left: 50px;
}

.s3 {
  left: 100px;
}

.s4 {
  left: 150px;
}

.s5 {
  left: 200px;
}

.z1 {
  top: 0px;
}

.z2 {
  top: 50px;
}

.z3 {
  top: 100px;
}

.z4 {
  top: 150px;
}

.z5 {
  top: 200px;
}
